/* Общие стили для страницы */
.generator-container {
  padding: 40px 20px;
  background-color: #f4f7fa;
  border-radius: 10px;
  margin: 0 auto;
}

.form-title {
  text-align: center;
}

.form-horizontal {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 60px;
  margin-top: 20px;
}
.form-horizontal form {
  flex-grow: 1;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}
.form-group label {
  font-size: 1.1rem;
  margin-bottom: 5px;
}
.form-group input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}


/* Результат */
.result-container {
  flex-grow: 1;
  border: 2px solid #ddd;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  max-width: 350px;
}

.result-container h2 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}
.result-container p {
  font-size: 1.1rem;
}

/* Кнопка "Назад" */
.btn-generator {
  display: inline-block;
  background-color: #f44336;
  color: white;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 30px;
}
.btn-generator:hover {
  background-color: #e53935;
}